<template>
  <div class="goodsattribute">
    <div id="leixin">
      <div class="search_container">
        <el-form :inline="true" :model="goodsattribute" class="demo-form-inline">
          <el-form-item label="所属公司" size="mini">
            <el-select clearable v-model="goodsattribute.company_id" placeholder="请选择" @change="companyChange" filterable>
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': ''">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="仓库名称" size="mini">
            <el-select clearable v-model="goodsattribute.warehouse_id" placeholder="请选择" @focus="getWarehouses" @change="warehouseChangeId">
              <el-option :label="item.warehouse_name" :value="item.warehouse_id" :key="item.warehouse_id" v-for="item in warehouseList.options"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="货位名称" size="mini">
            <el-select clearable class="el_slect" @focus="getAllocation" v-model="goodsattribute.allocation_id" placeholder="请选择" size="mini">
              <el-option :label="item.allocation_name" :value="item.allocation_id" :key="item.allocation_id" v-for="item in allocationInventoryList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="货位类型" size="mini">
            <el-select clearable v-model="goodsattribute.allocation_type" placeholder="请选择">
              <el-option :value="0" label="全部"></el-option>
              <el-option :value="1" label="固定货位"></el-option>
              <el-option :value="2" label="流动货位"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addF" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
        <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del==1">删除</el-button>
      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" v-loading="loading1" style="width: 100%" resizable max-height="600" @selection-change="handleSelectionChange">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
          </el-table-column>
          <el-table-column label="操作" fixed="right" width="54">
            <template slot-scope="scope">
              <el-button @click="handleEdit(scope.$index, scope.row)" type="text" size="small">
                修改
              </el-button>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="company_name" min-width="150" label="所属组织">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="warehouse_name" min-width="150" label="仓库名称">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="allocation_id" min-width="150" label="货位编号">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="allocation_name" min-width="150" label="货位名称">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="allocation_type" label="货位类型">
            <template slot-scope="scope">
              <span v-if="scope.row.allocation_type==1">固定货位</span>
              <span v-if="scope.row.allocation_type==2">流动货位</span>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="is_system" label="默认货位">
            <template slot-scope="scope">
              <span v-if="scope.row.is_system==1">是</span>
              <span v-if="scope.row.is_system==0">否</span>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="remark" label="备注" width="150">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="max_num" width="150" label="货位最大商品数" align="right">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_user_name" label="操作员">
            <template slot-scope="scope">
              <span v-if="scope.row.update_user_name!=''">{{scope.row.update_user_name}}</span>
              <span v-else>{{scope.row.add_user_name}}</span>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_time" label="添加日期" width="180">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="update_time" label="修改日期" width="180">
          </el-table-column>
        </el-table>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>

      <el-dialog :close-on-click-modal="false" title="货位管理" center :visible.sync="dialogFormVisible" width="450px">
        <el-form :model="ruleForm" ref="ruleForm" class="errorItem errorItemMl121">
          <el-form-item label="组织名称" prop="company_id" maxlength="50" :rules="rules.company_id">
            <el-select clearable :disabled="isdisable" v-model="ruleForm.company_id" placeholder="请选择" filterable @focus="getCompany" @change="clearWarehouse">
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': ''">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="所属仓库" prop="warehouse_id" :rules="rules.warehouse_id">
            <el-select :disabled="isdisable" clearable v-model="ruleForm.warehouse_id" @focus="getWarehouse" placeholder="请选择">
              <el-option :label="item.warehouse_name" :value="item.warehouse_id" :key="item.warehouse_id" v-for="item in warehouseList.options"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="货位名称" prop="allocation_name" :rules="[{ required: true, message: '请输入名称', trigger: 'blur' }]">
            <el-input v-model="ruleForm.allocation_name" placeholder="用'/'分割符号分割名称可批量添加'"></el-input>
          </el-form-item>
          <el-form-item label="货位类型" prop="allocation_type">
            <el-radio-group v-model="ruleForm.allocation_type" @change="changeAllocationType">
              <el-radio :label='1'> 固定货位</el-radio>
              <el-radio :label='2'> 流动货位</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" style="width: 150px;margin-left: 120px;" size="medium">
            <el-checkbox :false-label="0" :true-label="1" v-model="ruleForm.is_system">默认货位</el-checkbox>
          </el-form-item>
          <el-form-item label="货位最大商品数" size="medium" prop="max_num" :rules="rules.interNum" v-if="ruleForm.allocation_type==1">
            <el-input type="text" v-model="ruleForm.max_num" maxlength="300" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="备注" size="medium">
            <el-input type="text" v-model="ruleForm.remark" maxlength="300" placeholder="请输入"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogFormsubmit('ruleForm')">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import {
  getCompanyListCommon,
  bindIdsF,
  getDepartmentListCommon
} from "common/utils/public_function/commonUtils";
import { getData } from "common/utils/public_function/commonUtils";
export default {
  name: "systemInfoSalesStoragelocation",
  data() {
    return {
      authority: {},
      TableData: {
        tableData: [],
        per_page: [30, 50, 100, 500],
        total: 10
      },
      companyList: [],
      allocationInventoryList: [], //货位列表
      addOrchangeType: "add",
      AccordingParams: {}, //参数传递值
      goPage: 1, //默认搜索页面
      pageSize: 30, //默认表格size
      loading: true,
      per_page: [30, 50, 100, 500],
      allocation_name: null,
      warehouse: [],
      goodsattribute: {
        allocation_id: null,
        warehouse_id: null
      },
      isdisable: false,
      dialogFormVisible: false,
      ruleForm: {
        allocation_type: 1
      },
      warehouseList: {
        options: []
      }
    };
  },
  methods: {
    clearWarehouse() {
      this.$set(this.ruleForm, "warehouse_id", "");
    },
    getCompany() {
      getCompanyListCommon(this); //获取组织
    },
    changeAllocationType(val) {
      if (val == 1) {
        this.ruleForm.max_num = 1;
      }
    },
    warehouseChangeId(val) {
      this.$set(this.goodsattribute, "allocation_id", "");
    },
    companyChange(val) {
      this.$set(this.goodsattribute, "warehouse_id", "");
      this.$set(this.goodsattribute, "allocation_id", "");
    },
    allocation(val) {
      this.$set(this.params, "allocation_id", "");
      this.$axMethod
        .allocationInventoryList({
          warehouse_id: val
        })
        .then(({ data }) => {
          if (data.code == "200") {
            this.allocationInventoryList = data.data;
          } //货位列表下拉
        });
    },
    onReset() {
      this.$set(this, "goodsattribute", {});
      this.$set(this, "AccordingParams", {
        params: {},
        TableData: this.TableData, //列表数据
        API: "getAllocationList", //接口名
        vm: this //传递的this
      });
      getData(this.AccordingParams);
    },
    formatter(row, column) {
      if (row.is_system == 0) {
        return "否";
      } else if (row.is_system == 1) {
        return "是";
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleEdit(index, row) {
      this.dialogFormVisible = true;
      this.addOrchangeType = "change";
      this.isdisable = true;
      this.$set(this.warehouseList, "options", [
        {
          warehouse_id: row.warehouse_id,
          warehouse_name: row.warehouse_name
        }
      ]);
      this.$set(this, "companyList", [
        {
          company_id: row.company_id,
          company_name: row.company_name
        }
      ]);
      this.$nextTick(() => {
        this.ruleForm = Object.assign({}, row);
      });
    },
    onSearch() {
      Object.assign(this.AccordingParams.params, this.goodsattribute, {
        page: 1
      });
      getData(this.AccordingParams);
    },
    getAllocation() {
      this.$axMethod
        .allocationInventoryList({
          company_id: this.goodsattribute.company_id,
          warehouse_id: this.goodsattribute.warehouse_id
        })
        .then(({ data }) => {
          if (data.code == "200") {
            this.allocationInventoryList = data.data;
          } //货位列表下拉
        });
    },
    handleSizeChange(val) {
      Object.assign(this.AccordingParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.AccordingParams);
    },
    addF() {
      this.getCompany();
      this.isdisable = false;
      this.dialogFormVisible = true;
      this.addOrchangeType = "add";
      for (const key in this.ruleForm) {
        this.ruleForm[key] = "";
      }

      this.$nextTick(function() {
        this.$set(this.ruleForm, "allocation_type", 1);
      });
    },
    dialogFormsubmit(ruleForm) {
      if (this.ruleForm.allocation_type != 1) {
        this.ruleForm.max_num = 0;
      }
      var This = this;
      this.$refs[ruleForm].validate(valid => {
        if (valid) {
          if (This.addOrchangeType == "add") {
            let funcan = {
              eachData: this.ruleForm,
              UrlApi: "addAllocation",
              closeDialog: (this.dialogFormVisible = false),
              list: {
                UrlApi: "getAllocationList",
                data: this.TableData
                // esData: this.ruleForm
              }
            };
            this.$common.addAndupdateFun(funcan);
          } else {
            let funcan = {
              eachData: this.ruleForm,
              UrlApi: "editAllocation",
              closeDialog: (this.dialogFormVisible = false),
              list: {
                UrlApi: "getAllocationList",
                data: this.TableData
                // esData: this.ruleForm
              }
            };
            this.$common.addAndupdateFun(funcan);
          }
        } else {
          return false;
        }
      });
    },
    deletes() {
      var deledata = {
        deleID: "allocation_id",
        deleSelect: this.multipleSelection,
        deleApi: "delAllocation",
        list: {
          UrlApi: "getAllocationList",
          data: this.TableData
        }
      };
      this.$common.deletes(deledata, "删除", this);
    },
    handleCurrentChange(val) {
      Object.assign(this.AccordingParams.params, {
        page: val
      });
      getData(this.AccordingParams);
    },
    getWarehouse() {
      this.$common.options("getWarehouseListOption", this.warehouseList, {
        company_id: this.ruleForm.company_id,
        relation_warehouse_id: 1
      });
    },
    getWarehouses() {
      this.$common.options("getWarehouseListOption", this.warehouseList, {
        company_id: this.goodsattribute.company_id,
        relation_warehouse_id: 0
      });
    }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$set(this, "AccordingParams", {
      params: {}, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getAllocationList", //接口名
      vm: this //传递的this
    });
    getData(this.AccordingParams);
    getCompanyListCommon(this); //获取组织
  }
};
</script>
